window.preLoadImg = function() {    
	return new Promise((resolve, reject)=>{
	    	for (let i = 0; i <imgSource.length; i++) {    
			images[i] = new Image()    
			images[i].src = imgSource[i]    
			images[i].onload=function(){
	        		imgCompute.push(i);
	        		let len = imgCompute.length;
	        		if(len < imgSource.length){
	        			console.log(len/imgSource.length * 100);
	        			timeEl.innerText = (len/imgSource.length * 100).toFixed(0) + '%';
	        		}else if(len == imgCompute.length) {
	        			timeEl.innerText = 100 + '%';
	        			document.querySelector('.mask').classList.add('none');
					indexMusic();
	        			resolve();
	        		}
	      		}
	    	}   
	}) 
}


