// 文字逐个出现
function showText(el, text, num=1, stime) {
	el.innerText = text.substring(0, num);
	if(num < text.length && stime) {
		textTime = setTimeout(()=>{
			showText(el, text, num+1, stime)
		},50)
	}
}

// 音乐播放
function autoMusic() {
 	playMusic();
      document.addEventListener("WeixinJSBridgeReady", function () {
          	playMusic();
      }, false);
      document.removeEventListener('DOMContentLoaded', playMusic);
}
function playMusic() {
	indexVideo.pause();
	audio.play();
	musicEl.classList.add('active');
}
function stopMusic() {
	audio.pause();
	musicEl.classList.remove('active');
}

// 首页音乐
function indexMusic() {
	playIndex()
	document.addEventListener('WeixinJSBridgeReady', function(){
		playIndex();
	}, false)
	document.removeEventListener('DOMContentLoaded', playIndex);
}

function playIndex() {
	musicEl.classList.remove('active');
	swiperDom[0].classList.add('active');
	indexAnimation();
}


