// 数字动画
function numAction(el, targetNum, s) {
	let sTime = null; //定时器
	let n = 0; //动画初始化
	let _s = 10; //10 毫秒执行一次
	let incre = 1; //每一次的增量是多少 默认增量是1
	if(s){
		incre = Number(targetNum) / (Number(s)/_s);
	}else{
		incre = Number(targetNum) / (1000/_s);
	}
	sTime = setInterval(()=>{
		if(n  > Number(targetNum)) {
			clearInterval(sTime);
			sTime = null;
			el.innerHTML = targetNum;
		}else{
			n = n + incre;
			el.innerHTML = n.toFixed(0);
		}
	}, _s)
}





